.story{
    position: relative;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 120px;
    height: 200px;
    box-shadow: 0 5px 17px -7px rgba(0,0,0,0.5);
    border-radius: 10px;
    margin-right: 10px;
    transition: transform 100ms ease-in;
    cursor: pointer;
}
.story:hover{
    transform: scale(1.07);
}
.story_avatar{
    margin: 10px;
    border: 3px solid #2e81f5;
}
.story>h4{
    position: absolute;
    bottom: 20px;
    left: 10px;
    color: white;
}